import { NALU } from './nalu';
import { ExpGolomb } from './exp-golomb';
import { Log } from '../base/utils/log';
import { ErrorCode } from '../base/utils/error-map';
import { VideoSequence } from '../base/video-sequence';

type Units = Array<NALU>;
export interface H264Frame {
  units: Units;
  isKeyFrame: boolean;
}
export class H264Parser {
  public static getNaluFromSequence(sequence: VideoSequence): Array<Uint8Array> {
    return [
      ...sequence.headers.map((header) => new Uint8Array(header.data.data(), 4)),
      ...sequence.frames.map((frame) => new Uint8Array(frame.fileBytes.data.data(), 4)),
    ];
  }

  public static getH264Frames(nalus): H264Frame[] {
    if (nalus.length < 1) Log.error(ErrorCode.NotNalu);
    const frames: H264Frame[] = [];
    let units: Array<NALU> = [];
    let isKeyFrame = false;
    let isVcl = false;
    for (const nalu of nalus) {
      const unit = new NALU(nalu);
      if (unit.nalUnitType === NALU.IDR || unit.nalUnitType === NALU.NDR) {
        H264Parser.parseHeader(unit);
      }
      if (units.length && isVcl && (unit.firstMbInSlice || !unit.isVcl)) {
        frames.push({ units, isKeyFrame });
        units = [];
        isKeyFrame = false;
        isVcl = false;
      }
      units.push(unit);
      isKeyFrame = isKeyFrame || unit.isKeyframe();
      isVcl = isVcl || unit.isVcl;
    }
    if (units.length > 0) {
      if (isVcl) {
        frames.push({ units, isKeyFrame });
      } else {
        const last = frames.length - 1;
        frames[last].units = frames[last].units.concat(units);
      }
    }
    return frames;
  }

  public static parseHeader(nalu: NALU) {
    const decoder = new ExpGolomb(nalu.payload);
    decoder.readUByte(); // skip NALu type
    nalu.firstMbInSlice = decoder.readUEG() === 0;
    nalu.sliceType = decoder.readUEG();
  }

  public static parseSPS(nalu) {
    const { width, height } = H264Parser.readSPS(nalu);
    const sps = new Uint8Array(nalu);
    let codec = 'avc1.';
    const codecarray = new DataView(nalu.buffer, nalu.byteOffset + 1, 4);
    for (let i = 0; i < 3; ++i) {
      let h = codecarray.getUint8(i).toString(16);
      if (h.length < 2) {
        h = `0${h}`;
      }
      codec += h;
    }
    return { sps, codec, width, height };
  }

  public static skipScalingList(decoder, count) {
    let lastScale = 8;
    let nextScale = 8;
    let deltaScale;
    for (let j = 0; j < count; j++) {
      if (nextScale !== 0) {
        deltaScale = decoder.readEG();
        nextScale = (lastScale + deltaScale + 256) % 256;
      }
      lastScale = nextScale === 0 ? lastScale : nextScale;
    }
  }

  public static readSPS(data) {
    const profileIdcMap = [100, 110, 122, 244, 44, 83, 86, 118, 128];
    const decoder = new ExpGolomb(data);
    let frameCropLeftOffset = 0;
    let frameCropRightOffset = 0;
    let frameCropTopOffset = 0;
    let frameCropBottomOffset = 0;
    let sarScale = 1;
    let numRefFramesInPicOrderCntCycle;
    let scalingListCount;
    decoder.readBits(1); // forbidden_zero_bit
    decoder.readBits(2); // nal_ref_idc
    decoder.readBits(5); // nal_unit_type
    const profileIdc = decoder.readUByte(); // profile_idc
    decoder.readBits(6); // constraint_set[0-5]_flag, u(6)
    decoder.readBits(2); // reserved_zero_3bits u(2),
    decoder.readUByte(); // level_idc u(8)
    decoder.readUE(); // seq_parameter_set_id

    if (profileIdcMap.indexOf(profileIdc) > -1) {
      const chromaFormatIdc = decoder.readUE();
      if (chromaFormatIdc === 3) {
        decoder.skipBits(1); // separate_colour_plane_flag
      }
      decoder.readUE(); // bit_depth_luma_minus8
      decoder.readUE(); // bit_depth_chroma_minus8
      decoder.skipBits(1); // qpprime_y_zero_transform_bypass_flag
      const seqScalingMatrixPresentFlag = decoder.readBoolean();
      if (seqScalingMatrixPresentFlag) {
        scalingListCount = chromaFormatIdc !== 3 ? 8 : 12;
        for (let i = 0; i < scalingListCount; ++i) {
          if (decoder.readBoolean()) {
            // seq_scaling_list_present_flag[ i ]
            if (i < 6) {
              H264Parser.skipScalingList(decoder, 16);
            } else {
              H264Parser.skipScalingList(decoder, 64);
            }
          }
        }
      }
    }
    decoder.readUE(); // log2_max_frame_num_minus4
    const picOrderCntType = decoder.readUE();
    if (picOrderCntType === 0) {
      decoder.readUE(); // log2_max_pic_order_cnt_lsb_minus4
    } else if (picOrderCntType === 1) {
      decoder.skipBits(1); // delta_pic_order_always_zero_flag
      decoder.readUE(); // offset_for_non_ref_pic
      decoder.readUE(); // offset_for_top_to_bottom_field
      numRefFramesInPicOrderCntCycle = decoder.readUE();
      for (let i = 0; i < numRefFramesInPicOrderCntCycle; ++i) {
        decoder.readUE(); // offset_for_ref_frame[ i ]
      }
    }
    decoder.readUE(); // max_num_ref_frames
    decoder.skipBits(1); // gaps_in_frame_num_value_allowed_flag
    const picWidthInMbsMinus1 = decoder.readUE();
    const picHeightInMapUnitsMinus1 = decoder.readUE();
    const frameMbsOnlyFlag = decoder.readBits(1);
    if (frameMbsOnlyFlag === 0) {
      decoder.skipBits(1); // mb_adaptive_frame_field_flag
    }
    decoder.readBits(1); // direct_8x8_inference_flag
    const frameCroppingFlag = decoder.readBoolean();
    if (frameCroppingFlag) {
      frameCropLeftOffset = decoder.readUE();
      frameCropRightOffset = decoder.readUE();
      frameCropTopOffset = decoder.readUE();
      frameCropBottomOffset = decoder.readUE();
    }
    const vuiParametersPresentFlag = decoder.readBoolean();
    if (vuiParametersPresentFlag) {
      const aspectRatioInfoPresentFlag = decoder.readBoolean();
      if (aspectRatioInfoPresentFlag) {
        let sarRatio;
        const aspectRatioIdc = decoder.readUByte();
        switch (aspectRatioIdc) {
          case 1:
            sarRatio = [1, 1];
            break;
          case 2:
            sarRatio = [12, 11];
            break;
          case 3:
            sarRatio = [10, 11];
            break;
          case 4:
            sarRatio = [16, 11];
            break;
          case 5:
            sarRatio = [40, 33];
            break;
          case 6:
            sarRatio = [24, 11];
            break;
          case 7:
            sarRatio = [20, 11];
            break;
          case 8:
            sarRatio = [32, 11];
            break;
          case 9:
            sarRatio = [80, 33];
            break;
          case 10:
            sarRatio = [18, 11];
            break;
          case 11:
            sarRatio = [15, 11];
            break;
          case 12:
            sarRatio = [64, 33];
            break;
          case 13:
            sarRatio = [160, 99];
            break;
          case 14:
            sarRatio = [4, 3];
            break;
          case 15:
            sarRatio = [3, 2];
            break;
          case 16:
            sarRatio = [2, 1];
            break;
          case 255: {
            sarRatio = [
              (decoder.readUByte() << 8) | decoder.readUByte(),
              (decoder.readUByte() << 8) | decoder.readUByte(),
            ];
            break;
          }
        }
        if (sarRatio) {
          sarScale = sarRatio[0] / sarRatio[1];
        }
      }
    }
    return {
      width: Math.ceil(
        ((picWidthInMbsMinus1 + 1) * 16 - frameCropLeftOffset * 2 - frameCropRightOffset * 2) * sarScale,
      ),
      height:
        (2 - frameMbsOnlyFlag) * (picHeightInMapUnitsMinus1 + 1) * 16 -
        (frameMbsOnlyFlag ? 2 : 4) * (frameCropTopOffset + frameCropBottomOffset),
    };
  }
}
