import { Point, ZERO_POINT } from './point';
import { Property } from './property';
import { TimeRange } from './time-range';

export class Transform2D {
  public static createDefaultTransform2D() {
    const transform = new Transform2D();
    transform.anchorPoint = new Property<Point>();
    transform.anchorPoint.value = ZERO_POINT;
    transform.position = new Property<Point>();
    transform.position.value = ZERO_POINT;
    transform.xPosition = new Property<number>();
    transform.xPosition.value = 0;
    transform.yPosition = new Property<number>();
    transform.yPosition.value = 0;
    transform.scale = new Property<Point>();
    transform.scale.value = new Point(1, 1);
    transform.rotation = new Property<number>();
    transform.rotation.value = 0;
    transform.opacity = new Property<number>();
    transform.opacity.value = 255;
    return transform;
  }

  public anchorPoint: Property<Point> = undefined; // spatial
  public position: Property<Point> = undefined; // spatial
  public xPosition: Property<number> = undefined;
  public yPosition: Property<number> = undefined;
  public scale: Property<Point> = undefined; // multidimensional
  public rotation: Property<number> = undefined;
  public opacity: Property<number> = undefined;

  public excludeVaryingRanges(timeRanges: Array<TimeRange>): void {
    this.anchorPoint.excludeVaryingRanges(timeRanges);
    if (this.position !== undefined) {
      this.position.excludeVaryingRanges(timeRanges);
    } else {
      this.xPosition.excludeVaryingRanges(timeRanges);
      this.yPosition.excludeVaryingRanges(timeRanges);
    }
    this.scale.excludeVaryingRanges(timeRanges);
    this.rotation.excludeVaryingRanges(timeRanges);
    this.opacity.excludeVaryingRanges(timeRanges);
  }

  public gotoFrame(frame: number): void {
    this.anchorPoint.gotoFrame(frame);
    if (this.position !== undefined) {
      this.position.gotoFrame(frame);
    } else {
      this.xPosition.gotoFrame(frame);
      this.yPosition.gotoFrame(frame);
    }
    this.scale.gotoFrame(frame);
    this.rotation.gotoFrame(frame);
    this.opacity.gotoFrame(frame);
  }

  public verify(): boolean {
    return (
      this.anchorPoint !== undefined &&
      (this.position !== undefined || (this.xPosition !== undefined && this.yPosition !== undefined)) &&
      this.scale !== undefined &&
      this.rotation !== undefined &&
      this.opacity !== undefined
    );
  }
}
