import { RenderingMode, EventName, ScaleMode } from './view/types';
import { detectWebGLContext, createProgram, getShaderSourceFromString, getVideoAttributes } from './view/utils';
import { VERTEX_2D_SHADER, FRAGMENT_2D_SHADER, FRAGMENT_2D_SHADER_TRANSPARENT } from './view/shader';
import { Log } from './base/utils/log';
import { ErrorCode } from './base/utils/error-map';
import { Mp4Data } from './pag-generator';
import { EventManager, Listener } from './base/utils/event-manager';

declare global {
  interface Window {
    WebKitMediaSource: any;
  }
}

export interface RenderOptions {
  renderingMode?: RenderingMode;
  scaleMode?: ScaleMode;
  /**
   * 设置动画重复的次数。默认值为 1，只播放一次。如为 0 动画则无限播放。
   */
  repeatCount?: number;
}

export interface AlphaVideoAttributes {
  width: number; // PAG画布宽度
  height: number; // PAG画布高度
  videoWidth: number; // VideoComposition宽度
  videoHeight: number; // VideoComposition高度
  hasAlpha: boolean; // 是否有Alpha通道
  alphaStartX: number; // Alpha通道 X轴坐标
  alphaStartY: number; // Alpha通道 Y轴坐标
  sequenceWidth: number; // Sequence宽度
  sequenceHeight: number; // Sequence高度
  sequenceScale: { width: number; height: number }; // Sequence需要缩放大小
  position: { x: number; y: number }; // VideoComposition在PAG画布上的坐标
  mp4Width: number; // Mp4 video宽度
  mp4Height: number; // Mp4 video高度
}

export class PAGView {
  /**
   * 渲染类型的枚举
   */
  public static RenderingMode = RenderingMode;
  /**
   * 缩放模式的枚举
   */
  public static ScaleMode = ScaleMode;

  /**
   * 创建一个 PAGView 对象
   * @param mp4Data mp4数据
   * @param canvas 渲染画板
   * @param options 渲染选项
   * @returns PAGView 对象
   */
  public static create(mp4Data: Mp4Data, canvas: HTMLCanvasElement, options: RenderOptions): PAGView {
    const pagView = new PAGView(mp4Data, canvas, options);
    pagView.supportWebGL = detectWebGLContext();
    pagView.videoAttributes = getVideoAttributes(pagView.mp4Data);
    pagView.eventManager = new EventManager();
    pagView.canvas = canvas;
    pagView.videoEl = document.createElement('video');
    pagView.videoEl.style.display = 'none';
    pagView.videoEl.muted = true;
    pagView.videoEl.playsInline = true;
    const blob = new Blob([mp4Data.data], { type: 'video/mp4' });
    pagView.videoEl.src = URL.createObjectURL(blob);
    if (!!mp4Data.mp4Info.width) {
      pagView.videoAttributes.mp4Width = mp4Data.mp4Info.width;
    }
    if (!!mp4Data.mp4Info.height) {
      pagView.videoAttributes.mp4Height = mp4Data.mp4Info.height;
    }

    pagView.videoEl.addEventListener('play', () => {
      pagView.flushLoop(pagView, pagView.videoEl);
    });
    pagView.videoEl.addEventListener('pause', () => {
      pagView.clearRenderTimer();
    });
    pagView.videoEl.addEventListener('ended', () => {
      // 无限循环
      if (pagView.repeatCount < 0) {
        pagView.videoEl.play();
        pagView.eventManager.emit('onAnimationRepeat');
        return;
      }
      // 循环结束
      if (pagView.repeatCount === 0) {
        pagView.videoEl.currentTime = 0;
        pagView.clearRenderTimer();
        pagView.clearRender();
        pagView.playing = false;
        pagView.eventManager.emit('onAnimationEnd');
        return;
      }
      // 次数循环
      pagView.repeatCount -= 1;
      pagView.videoEl.play();
      pagView.eventManager.emit('onAnimationRepeat');
    });

    pagView.setRenderingMode(pagView.renderingMode);
    pagView.setScaleMode(options.scaleMode);
    if (pagView.renderingMode === RenderingMode.WebGL) pagView.loadWithWebGL();
    if (pagView.renderingMode === RenderingMode.Canvas) pagView.loadWithCanvas();
    return pagView;
  }

  private canvas: HTMLCanvasElement;
  private supportWebGL: boolean;
  private videoEl: HTMLVideoElement;
  private videoAttributes: AlphaVideoAttributes;
  private mp4Data: Mp4Data;
  private renderingMode: RenderingMode;
  private context2D: CanvasRenderingContext2D;
  private gl: WebGLRenderingContext;
  private program: WebGLProgram;
  private positionLocation = 0;
  private texcoordLocation = 0;
  private alphaStartLocation: WebGLUniformLocation;
  private scaleLocation: WebGLUniformLocation;
  private positionBuffer: WebGLBuffer;
  private texcoordBuffer: WebGLBuffer;
  private renderTimer: number | null = null;
  private renderCanvas2D: HTMLCanvasElement;
  private renderCanvas2DContext: CanvasRenderingContext2D;
  private playing = false;
  private destroyed = false;
  private repeatCount = 0; // 设置动画重复的次数。默认值为 0，只播放一次。如为 -1 动画则无限播放。
  private eventManager: EventManager;
  private viewScaleMode: ScaleMode;
  private viewportSize = { x: 0, y: 0, width: 0, height: 0, scaleX: 1, scaleY: 1 }; // viewport尺寸 WebGL坐标轴轴心在左下角|Canvas2D坐标轴轴心在左上角

  constructor(mp4Data: Mp4Data, canvas: HTMLCanvasElement, options: RenderOptions) {
    this.canvas = canvas;
    this.mp4Data = mp4Data;
    this.renderingMode = options.renderingMode || RenderingMode.WebGL;
    if (options.repeatCount !== undefined) {
      this.repeatCount = options.repeatCount < 0 ? -1 : options.repeatCount - 1;
    }
  }

  /**
   * 开始播放
   */
  public play() {
    if (this.destroyed) Log.error(ErrorCode.PagDestroyed);
    if (!!this.playing) return;
    this.videoEl.play();
    this.playing = true;
    this.eventManager.emit('onAnimationStart');
  }
  /**
   * 暂停播放
   */
  public pause() {
    if (this.destroyed) Log.error(ErrorCode.PagDestroyed);
    if (!this.playing) return;
    this.videoEl.pause();
    this.playing = false;
    this.eventManager.emit('onAnimationCancel');
  }
  /**
   * 停止播放
   */
  public stop() {
    if (this.destroyed) Log.error(ErrorCode.PagDestroyed);
    this.videoEl.pause();
    this.videoEl.currentTime = 0;
    this.clearRender();
    this.playing = false;
    this.eventManager.emit('onAnimationCancel');
  }
  /**
   * 销毁播放实例
   */
  public destroy() {
    if (this.destroyed) return;
    this.stop();
    this.canvas = null;
    this.videoEl = null;
    this.destroyed = true;
  }
  /**
   * 是否播放中
   */
  public isPlaying() {
    return this.playing;
  }
  /**
   * 是否已经销毁
   */
  public isDestroyed() {
    return this.destroyed;
  }
  /**
   * 动画持续时间
   */
  public duration() {
    return this.videoEl.duration;
  }
  /**
   * 设置动画重复的次数。默认值为 1，只播放一次。如为 0 动画则无限播放。
   */
  public setRepeatCount(repeatCount = 1) {
    this.repeatCount = repeatCount < 0 ? -1 : repeatCount - 1;
  }
  /**
   * 返回当前播放进度位置，取值范围为 0.0 到 1.0。
   */
  public getProgress() {
    return Math.round((this.videoEl.currentTime / this.videoEl.duration) * 100) / 100;
  }
  /**
   * 设置播放进度位置，取值范围为 0.0 到 1.0。
   */
  public setProgress(progress: number) {
    if (this.destroyed) Log.error(ErrorCode.PagDestroyed);
    if (progress < 0 || progress > 1) Log.error(ErrorCode.InvalidPercentage);
    if (this.playing) this.pause();
    this.videoEl.currentTime = progress * this.videoEl.duration;
    return new Promise((resolve) => {
      const timeupdateCallback = () => {
        this.flush(this.videoEl);
        this.videoEl.removeEventListener('timeupdate', timeupdateCallback);
        resolve(true);
      };
      this.videoEl.addEventListener('timeupdate', timeupdateCallback);
    });
  }
  /**
   * 渲染当前进度画面
   */
  public flush(videoEl: HTMLVideoElement) {
    this.eventManager.emit(EventName.beforeFlush);
    if (this.renderingMode === RenderingMode.WebGL) {
      this.renderWithWebGL(videoEl);
    } else {
      this.renderWithCanvas(videoEl);
    }
    this.eventManager.emit(EventName.afterFlush);
  }
  /**
   * 增加事件监听
   */
  public addListener(eventName: string, listener: Listener) {
    return this.eventManager.on(eventName, listener);
  }
  /**
   * 移除事件监听
   */
  public removeListener(eventName: string, listener?: Listener) {
    return this.eventManager.off(eventName, listener);
  }
  /**
   * 返回当前缩放模式
   */
  public scaleMode() {
    return this.viewScaleMode;
  }
  /**
   * 指定缩放内容的模式
   */
  public setScaleMode(scaleMode: ScaleMode = ScaleMode.LetterBox) {
    this.viewScaleMode = scaleMode;
    switch (scaleMode) {
      case ScaleMode.None:
        this.viewportSize = {
          x: this.videoAttributes.position.x,
          y:
            this.renderingMode === RenderingMode.WebGL
              ? this.gl.canvas.height - this.videoAttributes.position.y - this.videoAttributes.videoHeight
              : this.videoAttributes.position.y,
          width: this.videoAttributes.videoWidth,
          height: this.videoAttributes.videoHeight,
          scaleX: 1,
          scaleY: 1,
        };
        break;
      case ScaleMode.Stretch:
        {
          const scaleX = this.canvas.width / this.videoAttributes.width;
          const scaleY = this.canvas.height / this.videoAttributes.height;
          this.viewportSize = {
            x: this.videoAttributes.position.x * scaleX,
            y:
              this.renderingMode === RenderingMode.WebGL
                ? this.canvas.height -
                  this.videoAttributes.position.y * scaleY -
                  this.videoAttributes.videoHeight * scaleY
                : this.videoAttributes.position.y * scaleY,
            width: this.videoAttributes.videoWidth * scaleX,
            height: this.videoAttributes.videoHeight * scaleY,
            scaleX,
            scaleY,
          };
        }
        break;
      case ScaleMode.LetterBox:
        {
          const scaleX = this.canvas.width / this.videoAttributes.width;
          const scaleY = this.canvas.height / this.videoAttributes.height;
          const scale = Math.min(scaleX, scaleY);
          this.viewportSize = {
            x: (this.canvas.width - this.videoAttributes.width * scale) / 2 + this.videoAttributes.position.x * scale,
            y:
              this.renderingMode === RenderingMode.WebGL
                ? this.canvas.height -
                  (this.canvas.height - this.videoAttributes.height * scale) / 2 -
                  this.videoAttributes.position.y * scale -
                  this.videoAttributes.videoHeight * scale
                : (this.canvas.height - this.videoAttributes.height * scale) / 2 +
                  this.videoAttributes.position.y * scale,
            width: this.videoAttributes.videoWidth * scale,
            height: this.videoAttributes.videoHeight * scale,
            scaleX: scale,
            scaleY: scale,
          };
        }
        break;
      case ScaleMode.Zoom:
        {
          const scaleX = this.canvas.width / this.videoAttributes.width;
          const scaleY = this.canvas.height / this.videoAttributes.height;
          const scale = Math.max(scaleX, scaleY);
          this.viewportSize = {
            x: (this.canvas.width - this.videoAttributes.width * scale) / 2 + this.videoAttributes.position.x * scale,
            y:
              this.renderingMode === RenderingMode.WebGL
                ? this.canvas.height -
                  (this.canvas.height - this.videoAttributes.height * scale) / 2 -
                  this.videoAttributes.position.y * scale -
                  this.videoAttributes.videoHeight * scale
                : (this.canvas.height - this.videoAttributes.height * scale) / 2 +
                  this.videoAttributes.position.y * scale,
            width: this.videoAttributes.videoWidth * scale,
            height: this.videoAttributes.videoHeight * scale,
            scaleX: scale,
            scaleY: scale,
          };
        }
        break;
      default:
        break;
    }
  }

  private clearRender() {
    if (this.destroyed) Log.error(ErrorCode.PagDestroyed);

    if (this.renderingMode === RenderingMode.WebGL) {
      this.clearRenderWithWebGL();
    } else {
      this.clearRenderWithCanvas();
    }
  }

  private setRenderingMode(renderingMode: RenderingMode) {
    if (renderingMode === RenderingMode.WebGL) {
      if (this.supportWebGL === false) {
        this.renderingMode = RenderingMode.Canvas;
        this.setRenderingMode(RenderingMode.Canvas);
        return;
      }
      if (!this.gl) {
        this.gl = this.canvas.getContext('webgl', {});
      }
      if (!this.gl) {
        this.renderingMode = RenderingMode.Canvas;
        this.setRenderingMode(RenderingMode.Canvas);
      } else {
        if (!!this.videoAttributes.hasAlpha) {
          this.program = createProgram(
            this.gl,
            getShaderSourceFromString(VERTEX_2D_SHADER),
            getShaderSourceFromString(FRAGMENT_2D_SHADER_TRANSPARENT),
          );
        } else {
          this.program = createProgram(
            this.gl,
            getShaderSourceFromString(VERTEX_2D_SHADER),
            getShaderSourceFromString(FRAGMENT_2D_SHADER),
          );
        }
      }
    } else {
      this.context2D = this.canvas.getContext('2d');
    }
  }

  private loadWithWebGL() {
    // look up where the vertex data needs to go.
    this.positionLocation = this.gl.getAttribLocation(this.program, 'a_position');
    if (this.positionLocation === -1) throw new Error('unable to get attribute location for a_position');

    this.scaleLocation = this.gl.getUniformLocation(this.program, 'u_scale');
    if (this.scaleLocation === -1) throw new Error('unable to get attribute location for u_scale');

    this.texcoordLocation = this.gl.getAttribLocation(this.program, 'a_texCoord');
    if (this.texcoordLocation === -1) throw new Error('unable to get attribute location for a_texCoord');

    this.alphaStartLocation = this.gl.getUniformLocation(this.program, 'v_alphaStart');
    if (this.alphaStartLocation === -1) throw new Error('unable to get attribute location for v_alphaStart');

    // Create a buffer to put three 2d clip space points in
    this.positionBuffer = this.gl.createBuffer();

    // Bind it to ARRAY_BUFFER (think of it as ARRAY_BUFFER = positionBuffer)
    this.gl.bindBuffer(this.gl.ARRAY_BUFFER, this.positionBuffer);
    // Set a rectangle the same size as the image.
    this.setRectangle(this.gl, 0, 0, this.videoAttributes.mp4Width, this.videoAttributes.mp4Height);

    // provide texture coordinates for the rectangle.
    this.texcoordBuffer = this.gl.createBuffer();
    this.gl.bindBuffer(this.gl.ARRAY_BUFFER, this.texcoordBuffer);

    // Create a texture.
    const texture: WebGLTexture = this.gl.createTexture();
    this.gl.bindTexture(this.gl.TEXTURE_2D, texture);

    // Set the parameters so we can render any size video.
    this.gl.texParameteri(this.gl.TEXTURE_2D, this.gl.TEXTURE_WRAP_S, this.gl.CLAMP_TO_EDGE);
    this.gl.texParameteri(this.gl.TEXTURE_2D, this.gl.TEXTURE_WRAP_T, this.gl.CLAMP_TO_EDGE);
    this.gl.texParameteri(this.gl.TEXTURE_2D, this.gl.TEXTURE_MIN_FILTER, this.gl.LINEAR);
    this.gl.texParameteri(this.gl.TEXTURE_2D, this.gl.TEXTURE_MAG_FILTER, this.gl.LINEAR);

    this.gl.bufferData(
      this.gl.ARRAY_BUFFER,
      new Float32Array([
        0 + 0.5 / this.videoAttributes.mp4Width,
        0.0 + 0.5 / this.videoAttributes.mp4Height,
        1.0 - 0.5 / this.videoAttributes.mp4Width,
        0.0 + 0.5 / this.videoAttributes.mp4Height,
        0.0 + 0.5 / this.videoAttributes.mp4Width,
        1.0 - 0.5 / this.videoAttributes.mp4Height,
        0.0 + 0.5 / this.videoAttributes.mp4Width,
        1.0 - 0.5 / this.videoAttributes.mp4Height,
        1.0 - 0.5 / this.videoAttributes.mp4Width,
        0.0 + 0.5 / this.videoAttributes.mp4Height,
        1.0 - 0.5 / this.videoAttributes.mp4Width,
        1.0 - 0.5 / this.videoAttributes.mp4Height,
      ]),
      this.gl.STATIC_DRAW,
    );
  }

  private loadWithCanvas() {
    this.renderCanvas2D = document.createElement('canvas');
    this.renderCanvas2D.width =
      this.videoAttributes.mp4Width * this.videoAttributes.sequenceScale.width * this.viewportSize.scaleX;
    this.renderCanvas2D.height =
      this.videoAttributes.mp4Height * this.videoAttributes.sequenceScale.height * this.viewportSize.scaleY;
    this.renderCanvas2DContext = this.renderCanvas2D.getContext('2d');
  }

  private setRectangle(gl: WebGLRenderingContext, x: number, y: number, width: number, height: number) {
    const x1: number = x;
    const x2: number = x + width;
    const y1: number = y;
    const y2: number = y + height;
    gl.bufferData(gl.ARRAY_BUFFER, new Float32Array([x1, y1, x2, y1, x1, y2, x1, y2, x2, y1, x2, y2]), gl.STATIC_DRAW);
  }

  private flushLoop(pagView: PAGView, videoEl: HTMLVideoElement) {
    pagView.flush(videoEl);
    this.renderTimer = window.requestAnimationFrame(() => {
      this.flushLoop(pagView, videoEl);
    });
  }

  private renderWithWebGL(videoEl: HTMLVideoElement) {
    // Upload the video into the texture.
    this.gl.texImage2D(this.gl.TEXTURE_2D, 0, this.gl.RGBA, this.gl.RGBA, this.gl.UNSIGNED_BYTE, videoEl);

    // lookup uniforms
    const resolutionLocation = this.gl.getUniformLocation(this.program, 'u_resolution');

    // Tell WebGL how to convert from clip space to pixels
    this.gl.viewport(this.viewportSize.x, this.viewportSize.y, this.viewportSize.width, this.viewportSize.height);

    // Clear the canvas
    this.gl.clearColor(0, 0, 0, 0);
    this.gl.clear(this.gl.COLOR_BUFFER_BIT);

    // Tell it to use our program (pair of shaders)
    this.gl.useProgram(this.program);

    // Turn on the position attribute
    this.gl.enableVertexAttribArray(this.positionLocation);

    // Bind the position buffer
    this.gl.bindBuffer(this.gl.ARRAY_BUFFER, this.positionBuffer);

    // Tell the position attribute how to get data out of positionBuffer (ARRAY_BUFFER)
    const size = 2; // 2 components per iteration
    const type: number = this.gl.FLOAT; // the data is 32bit floats
    const normalize = false; // don't normalize the data
    const stride = 0; // 0 = move forward size * sizeof(type) each iteration to get the next position
    const offset = 0; // start at the beginning of the buffer
    this.gl.vertexAttribPointer(this.positionLocation, size, type, normalize, stride, offset);

    // Turn on the teccord attribute
    this.gl.enableVertexAttribArray(this.texcoordLocation);

    // Bind the position buffer.
    this.gl.bindBuffer(this.gl.ARRAY_BUFFER, this.texcoordBuffer);

    this.gl.vertexAttribPointer(this.texcoordLocation, size, type, normalize, stride, offset);

    // set the resolution
    this.gl.uniform2f(resolutionLocation, this.videoAttributes.videoWidth, this.videoAttributes.videoHeight);

    if (!!this.videoAttributes.hasAlpha) {
      this.gl.uniform2f(
        this.alphaStartLocation,
        this.videoAttributes.alphaStartX / this.videoAttributes.mp4Width,
        this.videoAttributes.alphaStartY / this.videoAttributes.mp4Height,
      );
    }

    this.gl.uniform2f(
      this.scaleLocation,
      this.videoAttributes.sequenceScale.width,
      this.videoAttributes.sequenceScale.height,
    );

    // Draw the rectangle.
    const primitiveType: number = this.gl.TRIANGLES;
    const count = 6;
    this.gl.drawArrays(primitiveType, offset, count);
  }

  private renderWithCanvas(videoEl: HTMLVideoElement) {
    if (this.videoAttributes.videoWidth === 0 || this.videoAttributes.videoHeight === 0) {
      return;
    }
    if (!!this.videoAttributes.hasAlpha) {
      this.renderCanvas2DContext.clearRect(0, 0, this.renderCanvas2D.width, this.renderCanvas2D.height);
      this.renderCanvas2DContext.drawImage(videoEl, 0, 0, this.renderCanvas2D.width, this.renderCanvas2D.height);
      const frameOne = this.renderCanvas2DContext.getImageData(0, 0, this.viewportSize.width, this.viewportSize.height);
      const frameTwo = this.renderCanvas2DContext.getImageData(
        this.videoAttributes.alphaStartX * this.videoAttributes.sequenceScale.width * this.viewportSize.scaleX,
        this.videoAttributes.alphaStartY * this.videoAttributes.sequenceScale.height * this.viewportSize.scaleY,
        this.viewportSize.width,
        this.viewportSize.height,
      );
      const length = frameOne.data.length / 4;
      for (let i = 0; i < length; i++) {
        frameOne.data[i * 4 + 3] = frameTwo.data[i * 4 + 0];
      }
      this.context2D.clearRect(0, 0, this.canvas.width, this.canvas.height);
      this.context2D.putImageData(
        frameOne,
        this.viewportSize.x,
        this.viewportSize.y,
        0,
        0,
        this.viewportSize.width,
        this.viewportSize.height,
      );
    } else {
      this.context2D.drawImage(
        videoEl,
        0,
        0,
        this.videoAttributes.sequenceWidth,
        this.videoAttributes.sequenceHeight,
        this.videoAttributes.position.x,
        this.videoAttributes.position.y,
        this.videoAttributes.videoWidth,
        this.videoAttributes.videoHeight,
      );
    }
  }

  private clearRenderWithWebGL() {
    this.gl.clearColor(0, 0, 0, 0);
    this.gl.clear(this.gl.COLOR_BUFFER_BIT);
  }

  private clearRenderWithCanvas() {
    this.context2D.clearRect(0, 0, this.canvas.width, this.canvas.height);
  }

  private clearRenderTimer() {
    if (this.renderTimer) {
      window.cancelAnimationFrame(this.renderTimer);
      this.renderTimer = null;
    }
  }
}
