export type Listener = (...payload: any) => void;

interface ListenersMap {
  [propName: string]: Listener[];
}

export class EventManager {
  private listenersMap: ListenersMap;

  constructor() {
    this.listenersMap = {};
  }

  on(evnetName: string, linstener: Listener) {
    if (this.listenersMap[evnetName] === undefined) {
      this.listenersMap[evnetName] = [];
    }
    this.listenersMap[evnetName].push(linstener);
    return;
  }

  off(evnetName: string, linstener?: Listener) {
    const listenerList: Listener[] = this.listenersMap[evnetName];
    if (listenerList === undefined) return;
    if (linstener === undefined) {
      delete this.listenersMap[evnetName];
      return;
    }
    const index = listenerList.findIndex((fn: Listener) => fn === linstener);
    listenerList.splice(index, 1);
    return;
  }

  emit(evnetName: string, ...payload: any): boolean {
    const listenerList: Listener[] = this.listenersMap[evnetName];
    if (listenerList === undefined || listenerList.length < 1) return false;
    for (const listener of listenerList) {
      listener(...payload);
    }
    return true;
  }
}
