import { PAGCodec } from './pag-codec';
import { Composition } from './base/composition';
import { ImageBytes } from './base/image-bytes';
import { request } from './base/utils/http-request';
import { Log } from './base/utils/log';
import { LayerType } from './base/layer';
import { VectorComposition } from './base/vector-composition';
import { ByteArray } from './codec/utils/byte-array';
import { CompositionType } from './codec/types';
import { TimeRange } from './base/time-range';
import { PreComposeLayer } from './base/pre-compose-layer';
import { VideoData } from './file/video-data';
import { isPAGFile } from './file/utils';
import { ErrorCode } from './base/utils/error-map';
import { Mp4Data, PAGGenerator } from './pag-generator';

export class PAGFile {
  public static async loadFile(url: string | File): Promise<Mp4Data> {
    let file: File;
    if (typeof url === 'string' && url !== '') {
      file = await this.loadFileByXHR(url);
    } else if (url instanceof File && isPAGFile(url)) {
      file = url;
    } else {
      Log.error(ErrorCode.InputError);
    }
    const pagFile = await this.readFileAsPAGFile(file);
    const videoData = PAGFile.pagFile2videoData(pagFile);
    return PAGGenerator.generatorMP4(videoData);
  }

  public static loadFileByXHR(url: string): Promise<File> {
    return new Promise((resolve) => {
      request({
        url,
        method: 'GET',
        responseType: 'blob',
        success: (response: Blob) => {
          if (!response) {
            Log.error(ErrorCode.LoadFileNotResponse);
          }
          const file = new window.File([response], url.replace(/(.*\/)*([^.]+)/i, '$2'));
          resolve(file);
        },
        error: () => {
          Log.error(ErrorCode.LoadFileByXhrError);
        },
      });
    });
  }

  public static readFileAsPAGFile(file: File): Promise<PAGFile> {
    return new Promise((resolve) => {
      const reader = new FileReader();
      reader.onload = () => {
        const dataBuffer: ArrayBuffer = reader.result as ArrayBuffer;
        const byteArray = new ByteArray(dataBuffer, true);
        const { compositions, images, tagLevel } = PAGCodec.decode(byteArray);
        const pagFile = new PAGFile(compositions, images);
        pagFile.tagLevel = tagLevel;
        resolve(pagFile);
      };
      reader.onerror = () => {
        Log.error(ErrorCode.ReadPagFileError);
      };
      reader.readAsArrayBuffer(file);
    });
  }

  public static pagFile2videoData(file: PAGFile) {
    return VideoData.create(file);
  }
  public tagLevel = 1;
  public mainComposition: Composition;
  public compositions: Array<Composition> = [];
  public numLayers = 0;
  public images: Array<ImageBytes> = [];
  public duration: number;
  public implDuration: number;
  public scaledTimeRange: TimeRange = { start: 0, end: 0 };
  public rootLayer: PreComposeLayer;

  constructor(compositions: Array<Composition>, images: Array<ImageBytes>) {
    this.mainComposition = compositions[compositions.length - 1];
    this.scaledTimeRange.start = 0;
    this.scaledTimeRange.end = this.mainComposition.duration;
    this.compositions = compositions;
    this.images = images;
    this.duration = this.mainComposition.duration;
    this.implDuration = (this.mainComposition.duration * 1000) / this.mainComposition.frameRate;
    for (const composition of compositions) {
      if (composition.type() !== CompositionType.Vector) {
        this.numLayers += 1;
        continue;
      }
      for (const layer of (composition as VectorComposition).layers) {
        if (layer.type() === LayerType.PreCompose) {
          continue;
        }
        this.numLayers += 1;
      }
    }
  }
}
