import { LayerType } from '../base/layer';
import { PreComposeLayer } from '../base/pre-compose-layer';
import { Transform2D } from '../base/transform-2d';
import { ErrorCode } from '../base/utils/error-map';
import { Log } from '../base/utils/log';
import { VectorComposition } from '../base/vector-composition';
import { VideoComposition } from '../base/video-composition';
import { VideoSequence } from '../base/video-sequence';
import { CompositionType } from '../codec/types';
import { concatArrayBuffers2Uint8Array } from '../codec/utils/byte-utils';
import { PAGFile } from '../pag-file';

export interface Matrix {
  width: number;
  height: number;
  frameRate: number;
  transform: Transform2D;
  duration: number;
  hasAlpha: boolean;
  videoWidth: number;
  videoHeight: number;
  alphaStartX?: number;
  alphaStartY?: number;
}

export class VideoData {
  public static create(file: PAGFile) {
    if (!file) Log.error(ErrorCode.NotPagFile);
    const videoData = new VideoData();
    if (file.mainComposition.type() === CompositionType.Video) {
      videoData.getVideoDataFromVideoComposition(file.mainComposition as VideoComposition);
    } else if (file.mainComposition.type() === CompositionType.Vector) {
      videoData.getVideoDataFromVectorComposition(file.mainComposition as VectorComposition);
    }
    return videoData;
  }

  public matrix: Matrix = {
    width: 0,
    height: 0,
    frameRate: 0,
    duration: 0,
    hasAlpha: false,
    transform: new Transform2D(),
    videoWidth: 0,
    videoHeight: 0,
  };
  public sequence: VideoSequence;

  public getH264Raw() {
    if (!this.sequence) Log.error(ErrorCode.NotSequence);
    const header = this.sequence.headers.map((header) => header.data.data());
    const body = this.sequence.frames.map((frame) => frame.fileBytes.data.data());
    const buffers = [...header, ...body];
    const h264Raw = concatArrayBuffers2Uint8Array(buffers);
    return h264Raw;
  }

  public getTPS() {
    if (!this.sequence) return;
    const tps = this.sequence.frames.map((frame) => frame.frame);
    return tps;
  }

  private getVideoDataFromVideoComposition(composition: VideoComposition) {
    if (composition.sequences.length < 1) Log.error(ErrorCode.NotSequence);
    this.matrix.width = composition.width;
    this.matrix.height = composition.height;
    this.matrix.frameRate = composition.frameRate;
    this.matrix.duration = composition.duration;
    this.matrix.videoWidth = composition.width;
    this.matrix.videoHeight = composition.height;
    this.matrix.hasAlpha = !!composition.hasAlpha;
    const sequence = composition.sequences[composition.sequences.length - 1];
    this.sequence = sequence;
    this.matrix.transform = Transform2D.createDefaultTransform2D();
  }

  private getVideoDataFromVectorComposition(composition: VectorComposition) {
    let sequencesCount = 0;
    for (const layer of composition.layers) {
      if (layer.type() !== LayerType.PreCompose) continue;

      const composition = (layer as PreComposeLayer).composition as VideoComposition;
      if (composition.type() === CompositionType.Video && composition.sequences.length >= 1) {
        sequencesCount += 1;
      }

      if (sequencesCount > 1) {
        Log.error(ErrorCode.NotSupportMultipleSequence);
      }
    }
    if (sequencesCount < 1) {
      Log.error(ErrorCode.NotSequence);
    }

    this.matrix.width = composition.width;
    this.matrix.height = composition.height;
    this.matrix.frameRate = composition.frameRate;
    this.matrix.duration = composition.duration;

    for (const layer of composition.layers) {
      if (layer.type() !== LayerType.PreCompose) continue;

      const videoComposition = (layer as PreComposeLayer).composition as VideoComposition;
      if (videoComposition.type() !== CompositionType.Video && videoComposition.sequences.length < 1) continue;
      this.matrix.videoWidth = videoComposition.width;
      this.matrix.videoHeight = videoComposition.height;
      this.matrix.hasAlpha = !!videoComposition.hasAlpha;
      const sequence = videoComposition.sequences[videoComposition.sequences.length - 1];
      this.sequence = sequence;
      this.matrix.transform = (layer as PreComposeLayer).transform;
      break;
    }
  }
}
