import { ByteArray } from './codec/utils/byte-array';
import { Log } from './base/utils/log';
import { readTags } from './codec/tags/tag-header';
import { readTagsOfFile } from './codec/tags/file-tags';
import { Composition } from './base/composition';
import { ImageBytes } from './base/image-bytes';
import { CompositionType, TagCode } from './codec/types';
import { VectorComposition } from './base/vector-composition';
import { LayerType } from './base/layer';
import { PreComposeLayer } from './base/pre-compose-layer';
import { ErrorCode } from './base/utils/error-map';

export const verifyAndMake = (compositions: Array<Composition>, images: Array<ImageBytes>) => {
  let success: boolean = compositions.length > 0;
  for (const composition of compositions) {
    if (!composition || !composition.verify()) {
      success = false;
      break;
    }
  }

  for (const imagBytes of images) {
    if (!imagBytes || !imagBytes.verify()) {
      success = false;
      break;
    }
  }

  if (!success) {
    compositions = undefined;
    images = undefined;
    return undefined;
  }
  return { compositions, images };
};

/**
 * 将Layer里面的compositionID标识换成真正的Composition
 */
export function installReference(compositions: Array<Composition>) {
  if (!compositions || compositions.length === 0) {
    return;
  }
  const compositionMap = new Map();
  for (const composition of compositions) {
    if (!composition) {
      continue;
    }
    compositionMap.set(composition.id, composition);
  }
  for (const item of compositions) {
    if (!item) {
      continue;
    }
    if (item.type() === CompositionType.Vector) {
      const vectorItem = item as VectorComposition;
      if (vectorItem.layers !== undefined && vectorItem.layers.length > 0) {
        for (const layer of vectorItem.layers) {
          layer.containingComposition = vectorItem;
          const preComposeLayer = layer as PreComposeLayer;
          if (preComposeLayer.type() === LayerType.PreCompose && preComposeLayer.composition !== undefined) {
            const ID = preComposeLayer.composition.id;
            const result = compositionMap.get(ID);
            if (result !== undefined) {
              preComposeLayer.composition = result;
            }
          }
        }
      }
    }
  }
}

export class PAGCodec {
  public static maxSupportedTagLevel(): number {
    return TagCode.Count - 1;
  }

  /**
   * 从特殊的字节流中解码出一个 PAG 文件
   */
  public static decode(byteArray: ByteArray) {
    if (!byteArray) Log.error(ErrorCode.NotByteArray);
    const bodyByteArray: ByteArray = this.readBodyBytes(byteArray);
    const { context } = bodyByteArray;
    readTags(bodyByteArray, context, readTagsOfFile);
    installReference(context.compositions);
    const { compositions, images } = verifyAndMake(context.releaseCompositions(), context.releaseImages());
    return { compositions, images, tagLevel: context.tagLevel };
  }

  public static readBodyBytes(byteArray: ByteArray): ByteArray {
    if (byteArray.length < 11) Log.error(ErrorCode.PagFileLengthErrorTooShort);

    const P: number = byteArray.readInt8();
    const A: number = byteArray.readInt8();
    const G: number = byteArray.readInt8();
    if (P !== 80 || A !== 65 || G !== 71) {
      Log.error(ErrorCode.InvalidPagFileHeader);
    }
    byteArray.readInt8(); // version
    byteArray.readUint32(); // bodyLength
    byteArray.readInt8(); // compression
    return byteArray.readBytes();
  }
}
